.temperature {
    font-size: 14px;
    font-weight: normal;
    letter-spacing: 3px;
}

.weather {
    font-size: 12px;
    font-weight: lighter;
}

img {
    height: 70px;
    width: 70px;
}
.center {
    margin: auto;
    width: 50%;
  }

::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    font-family: 'Roboto', sans-serif;
    color: white;
    font-size: 18px;
    letter-spacing: 1.4px;
}

:-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    font-family: 'Roboto', sans-serif;
    color: white;
    font-size: 18px;
    letter-spacing: 1.4px;
}

::-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    font-family: 'Roboto', sans-serif;
    color: white;
    font-size: 18px;
    letter-spacing: 1.4px;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    font-family: 'Roboto', sans-serif;
    color: white;
    font-size: 18px;
    letter-spacing: 1.4px;
}

input {
    background-color: transparent;
    color: white;
    border: none;
    font-family: 'Roboto', sans-serif;
    letter-spacing: 1.4px;
    font-size: 18px;
    padding: 5px;
    padding-left: 10px;
}

button {
    background-color: transparent;
    color: white;
    border: none;
    outline: none;
}
